  methods: {
    dealData (data) {
      if (data.data.code === 200) {
        this.footerData = data.data.data.desc
      }
    },
    footerMethod ({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "页小计";
          }
          if (["goods_num", "subtotal_cost_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "subtotal_cost_price") {
              return num
                .toFixed(2)
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            if (column.property === "goods_num") {
              return num
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            return Number.isInteger(num) ? num : num.toFixed(2);
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (column.property === 'goods_num' && this.footerData.total_goods_num !== undefined) {
            return this.footerData.total_goods_num
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          }
          if (column.property === 'subtotal_cost_price' && this.footerData.total_subtotal_cost_price !== undefined) {
            return (this.footerData.total_subtotal_cost_price - 0)
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          }
          if (["goods_num", "subtotal_cost_price"].includes(column.property)) {
            return '加载中...'
          }
          return null;
        })]
    }
  }
