				<el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
					<el-form-item label="调出组织" size="mini">
						<el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" filterable @change="outcanpanychange">
							<el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
								<span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="调入组织" size="mini">
						<el-select clearable v-model="params.new_company_id" placeholder="请选择" @focus="companyListInvok('', 'newCompanyList')" filterable @change="pullcanpanychange">
							<el-option v-for="item in newCompanyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
								<span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="业务日期" size="mini">
						<el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
						</el-date-picker>
					</el-form-item>
					<el-form-item label="单据号" size="mini">
						<el-input v-model="params.transfer_sn" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
					</el-form-item>
					<el-form-item label="单据来源" size="mini">
						<el-select clearable v-model="params.source" placeholder="请选择">
							<el-option label="全部" :value="0">全 部</el-option>
							<el-option label="ERP" :value="2">ERP</el-option>
							<el-option label="WMS" :value="1">WMS</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="审核状态" size="mini">
						<el-select clearable v-model="params.auditor_status" placeholder="请选择">
							<el-option label="全部" :value="0">全部</el-option>
							<el-option label="未审核" :value="1">未审核</el-option>
							<el-option label="已审核" :value="2">已审核</el-option>
							<el-option label="作废" :value="3">作废</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="商品编号" size="mini">
						<el-input v-model="params.goods_id" placeholder="请输入" maxLength="7" clearable @keyup.enter.native="onSearch(1)"></el-input>
					</el-form-item>
					<el-form-item label="商品名称" size="mini">
						<el-input v-model="params.goods_name" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
					</el-form-item>
					<el-form-item label="年周/批次" size="mini">
						<el-input v-model.trim="params.batch" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
					</el-form-item>
					<el-form-item label="调出仓库" size="mini">
						<el-select clearable v-model="params.warehouse_id" placeholder="请选择" filterable @focus="getWarehouseFouse" @change="warehouseChange">
							<el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="调出货位" size="mini">
						<el-select clearable v-model="params.allocation_id" @focus="allocationFocus" filterable placeholder="请选择">
							<el-option v-for="item in allocationList.options" :key="item.allocation_id" :label="item.allocation_name" :value="item.allocation_id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="调入仓库" size="mini">
						<el-select clearable v-model="params.new_warehouse_id" @focus="getNewWarehouseFouse" filterable placeholder="请选择">
							<el-option v-for="item in newWarehouseList.options" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="调入货位" size="mini">
						<el-select clearable v-model="params.new_allocation_id" @focus="allocationNewFocus" filterable placeholder="请选择">
							<el-option v-for="item in newAllocationList.options" :key="item.allocation_id" :label="item.allocation_name" :value="item.allocation_id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item size="mini" class="search-style search">
						<el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
						<el-button plain @click="onReset" size="mini">重置</el-button>
					</el-form-item>
				</el-form>
			</div>
			<div class="add_delet" v-if="authority.b_export==1">
				<el-dropdown placement="bottom-start">
					<span split-button style="cursor:pointer;color:#4e90e2">
						<el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
					</span>
					<el-dropdown-menu slot="dropdown" style="margin-top:10px;">
						<el-dropdown-item @click.native="exporTransfer(1)">导出Excel文件</el-dropdown-item>
						<el-dropdown-item @click.native="exporTransfer(2)">导出CSV逗号分隔</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
			<div class="table_container">
				<xTable :ajax='{ url: "getTransferDetailList", params: params }' @getData='dealData' :sign="indexSign" :rowClassName='{name: "sign_row",isDisable: false}' ref='xTable' :footerMethod='footerMethod' :tableColumn='tableColumn' />
			</div>
			<div class="table_info">
				<span>制表时间：{{add_time}}</span>
				<span style="float:right">制表人：{{add_user_name}}</span>
			</div>

		</div>
	</div>
</template>

<script>
	import getAreaList from "common/components/public/getAreaList";
	import { TableHead } from "./config/transferwareDetail";
	export default {
		name: "instorageTransferwareTransferwareDetail",
		mixins: [TableHead],
		data() {
			return {
				authority: {},

				postageTypeList: {
					options: []
				},
				params: {
					auditor_status: 0
				},
				loading: true,
				timeValue: [],
				companyList: [], // 调出组织
				newCompanyList: [], //  调入组织
				warehouseList: {
					options: []
				}, //调出仓库
				newWarehouseList: {
					options: []
				}, // 调入仓库
				multipleSelection: [], //已选项
				allocationList: {
					options: [] //调出货位
				},
				newAllocationList: {
					options: [] // 调入货位
				},
				add_user_name: "",
				add_time: ""
			};
		},
		methods: {
			outcanpanychange(val) {
				this.$set(this.params, "warehouse_id", "");
				this.$set(this.params, "allocation_id", "");
			},
			pullcanpanychange(val) {
				this.$set(this.params, "new_warehouse_id", "");
				this.$set(this.params, "new_allocation_id", "");
			},
			warehouseChange(val) {
				if (val) {
					this.params.warehouse_name = this.warehouseList.options.filter(
						obj => obj.warehouse_id === val
					)[0].warehouse_name;
				}
			},
			getWarehouseFouse() {
				this.$common.options("getWarehouseListOption", this.warehouseList, {
					company_id: this.params.company_id
				}); //仓库下拉列表
			},
			getNewWarehouseFouse() {
				this.$common.options("getWarehouseListOption", this.newWarehouseList, {
					company_id: this.params.new_company_id
				}); //仓库下拉列表
			},
			allocationFocus() {
				this.$axMethod
					.allocationInventoryList({
						warehouse_id: this.params.warehouse_id ? this.params.warehouse_id : ""
					})
					.then(({ data }) => {
						if (data.code == "200") {
							this.allocationList.options = data.data;
						} //货位列表下拉
					});
			},
			allocationNewFocus() {
				this.$axMethod
					.allocationInventoryList({
						warehouse_id: this.params.new_warehouse_id
							? this.params.new_warehouse_id
							: ""
					})
					.then(({ data }) => {
						if (data.code == "200") {
							this.newAllocationList.options = data.data;
						} //货位列表下拉
					});
			},
			exporTransfer(scheduleType) {
				let s = Object.assign(this.params, {
					export_type: scheduleType
				});
				this.$axMethod.exportTransferDetailUrl(this.params).then(res => {
					if (res.data.code == 200) {
						window.location.href = res.data.data.url;
					}
				});
			},
			warehouseFocus() {
				this.$common.options("getWarehouseListOption", this.warehouseList, {
					company_id: this.params.company_id
				});
			},
			onReset() {
				this.$set(this, "params", {
					auditor_status: 0
				});
				this.timeValue = [];
				this.onSearch(1);
			},
			onSearch(number) {
				if (this.timeValue) {
					this.params.start_time = this.timeValue[0];
					this.params.end_time = this.timeValue[1];
				} else {
					this.params.start_time = this.params.end_time = "";
				}
				this.footerData = {};
				this.$refs.xTable._proxy("updateFooter");
				this.$refs.xTable._getData(number);
			},
			dateFtt(fmt, date) {
				//author: meizz
				var o = {
					"M+": date.getMonth() + 1, //月份
					"d+": date.getDate(), //日
					"h+": date.getHours(), //小时
					"m+": date.getMinutes(), //分
					"s+": date.getSeconds(), //秒
					S: date.getMilliseconds() //毫秒
				};
				if (/(y+)/.test(fmt))
					fmt = fmt.replace(
						RegExp.$1,
						(date.getFullYear() + "").substr(4 - RegExp.$1.length)
					);
				for (var k in o)
					if (new RegExp("(" + k + ")").test(fmt))
						fmt = fmt.replace(
							RegExp.$1,
							RegExp.$1.length == 1
								? o[k]
								: ("00" + o[k]).substr(("" + o[k]).length)
						);
				return fmt;
			}
		},
		created() {
			this.companyListInvok();
			this.authority = JSON.parse(localStorage.getItem("authority"));
			this.add_user_name = localStorage.getItem("employee_name");
			let crtTime = new Date();
			this.add_time = this.dateFtt("yyyy-MM-dd hh:mm:ss", crtTime);
		},
		activated() {
			this.onSearch();
		}
	};
</script>
